// $global-width: rem-calc(1200);
// $global-lineheight: 1.5;
$foundation-palette: (
  primary: #0067ff,
  secondary: #73d2cf,
  success: #3adb76,
  warning: #ffae00,
  alert: #cc4b37,
);
