// Nuxeo Colours
// Colours
$white: #fff;
$black: #000;




$nuxeo-color-darker-gray: #333;

$nuxeo-color-accent: #ff452a;
$nuxeo-color-accent-yellow: #e8ff7f;

$nuxeo-color-light-blue: #ecf3f7;
$nuxeo-color-dark-background: #000522;

// Code Block
$nuxeo-color-code-background: $white;
$nuxeo-color-code-foreground: $black;
$nuxeo-color-code-border: #d9e6f0;
$code-border-radius: 4px;


// Tooltips
$tooltip-background-color: rgba(0, 0, 0, .8);
$tooltip-text-color: $white;
$tooltip-duration: .4s;
$tooltip-delay: .2s;


