$header-styles: (
  small: (
    'h1': ('font-size': 100),
    'h2': ('font-size': 72),
    'h3': ('font-size': 48),
    'h4': ('font-size': 32),
    'h5': ('font-size': 20),
    'h6': ('font-size': 16),
  ),
  medium: (
    'h1': ('font-size': 100),
    'h2': ('font-size': 78),
    'h3': ('font-size': 54),
    'h4': ('font-size': 36),
    'h5': ('font-size': 26),
    'h6': ('font-size': 22),
  ),
);
